import React from "react";
import Close from "../../img/icon/close.png";
import { deleteQuestion } from "../../api/Q&A/deleteQuestion";
import PropTypes from 'prop-types';

function QDelete({ questionId,  onClose , spaceId, userId}) {

  
    const delete_ = async () => {
      try {
        await deleteQuestion(spaceId, questionId, userId);
        onClose(); //     .
        window.location.reload();
      } catch (error) {
        if (error.response && error.response.status === 401) {
          console.error(" .");
          //      .
        } else {
          console.error("  :", error);
          //     .
        }
      }
    };


  return (
    <div className="popupWrap">
      <div className="popup">
        <div className="popupContainer">
          <div className="popupHeader">
            <img src={Close} alt="close" onClick={onClose} />
          </div>
          <div className="popupContent delPopup">
            <p className="popupTitle">  ?</p>
            <p className="subTitle">
                  .
            </p>
          </div>
          <div className="popupFooter">
            <button className="btnRed" onClick={delete_}></button>
            <button className="btnCancel" onClick={onClose}>
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

QDelete.propTypes={
  onClose: PropTypes.any.isRequired,
  questionId: PropTypes.any.isRequired,  
  spaceId: PropTypes.any.isRequired,  
  userId: PropTypes.any.isRequired,  
}

export default QDelete;
