// HealthCheck.js
import React, { useState, useEffect } from 'react';
import { healthCheck } from '../api/healthCheck'; // `yourHealthCheckModule`     .

const HealthCheck = () => {
  const [isServerOnline, setServerOnline] = useState(false);

  useEffect(() => {
    const checkServerHealth = async () => {
      try {
        await healthCheck(); // healthCheck  
        setServerOnline(true);
      } catch (error) {
        setServerOnline(false);
      }
    };

    checkServerHealth();
    const intervalId = setInterval(checkServerHealth, 5000); //5   

    return () => clearInterval(intervalId);

  }, []);

  return (
    <div>
      {isServerOnline ? (
        <p>  .</p>
      ) : (
        <p>   .</p>
      )}
    </div>
  );
};

export default HealthCheck;
